<template>
  <div class="home page-container">
    <div
      class="banner overflow-hidden"
      :style="{ backgroundPosition: `${offsetX}px ${offsetY}px` }"
    ></div>
    <div
      class="page-head d-flex flex-direction-column align-items-center justify-content-center"
    >
      <p class="mt-0 mb-4 fw-7 fs-6">I'm Tian Chen</p>
      <p class="mt-0 mb-8 fw-1 fs-1">Java Engineer | Front-end Engineer</p>
      <div class="mt-4 d-flex justify-content-between">
        <a href="mailto:terrence2tian2chen@gmail.com" target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('mail.svg')"
        /></a>
        <a href="https://github.com/TheT92" target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('GithubFilled.svg')"
        /></a>
        <a
          href="https://www.linkedin.com/in/tian-chen-233037326/"
          target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('LinkedinFilled.svg')"
        /></a>
        <a
          href="https://drive.google.com/file/d/1YrEz6zBWJtlQpRzZ44v86NpPOJJ5GJkD/view"
          target="_blank"
          ><img class="social-icon" :src="getBg('cv.svg')"
        /></a>
      </div>
    </div>
    <div id="about" class="about content-card d-flex mb-8">
      <!-- <div class="photo"></div> -->
      <div class="flex-1 blur-card p-4">
        <h2 class="mt-0 mb-0">About Me</h2>
        <div class="pre-wrap" v-html="INTRODUCTION"></div>
      </div>
    </div>
    <div id="projects" class="projects content-card clear-float mb-8">
      <h2 class="mt-0">Projects</h2>
      <div class="clear-float">
        <div
          v-for="(item, index) in MY_PROJECTS"
          :key="index"
          class="project-item blur-card p-4 mb-4 float-left overflow-hidden"
        >
          <router-link :to="item.link" class="text-decoration-none text-white">
            <h3 class="mt-0 mb-4 fs-2">&lt; {{ item.name }} &gt;</h3>

            <span
              class="d-inline-block ellipsis overflow-hidden mb-4"
              :alt="item.desc"
              >{{ item.desc }}</span
            >
            <div class="tags">
              <span
                v-for="(v, i) in item.keywords"
                :key="i"
                class="keyword fs-0"
                :style="{ backgroundColor: getColor(v) }"
              >
                {{ v }}
              </span>
            </div>
            <div class="cover-wrap overflow-hidden">
              <div
                class="cover-img"
                :style="{ backgroundImage: `url(${getBg(item.img)})` }"
              ></div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { INTRODUCTION, MY_PROJECTS } from "@/constant";
export default {
  name: "HomePage",
  components: {},
  data() {
    return {
      INTRODUCTION,
      MY_PROJECTS,
      windowWidth: 0,
      windowHeight: 0,
      offsetX: 0,
      offsetY: 0,
      activeIndex: 0,
    };
  },
  computed: {
    getBg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getColor() {
      return function (keyword) {
        if (keyword == "Java") {
          return "#ef8d07";
        } else if (keyword == "Spring") {
          return "#6db33f";
        } else if (keyword == "Vue") {
          return "#42b983";
        } else if (keyword == "ElementUI") {
          return "#409eff";
        } else if (keyword == "PostgreSQL") {
          return "#336791";
        } else if (keyword == "PHP") {
          return "#7a86b8";
        } else if (keyword == "Bootstrap") {
          return "#712cf9";
        } else if (keyword == "MySQL") {
          return "#3E6E93";
        } else if (keyword == "HTML") {
          return "#e54c21";
        } else if (keyword == "Unity") {
          return "#000000";
        } else if (keyword == "C++") {
          return "#048";
        } else if(keyword == 'git') {
          return "#f54d27";
        }else {
          return "#1890ff";
        }
      };
    },
  },
  methods: {
    onHover(index) {
      this.activeIndex = index;
    },
    updateMousePosition(event) {
      let x = Math.ceil((event.clientX - this.windowWidth / 2) / 20);
      let y = Math.ceil((event.clientY - this.windowHeight / 2) / 20);
      const banner = document.querySelector(".banner");
      const rect = banner.getBoundingClientRect();
      if (event.clientY >= rect.top && event.clientY <= rect.bottom) {
        x = x > 20 ? 20 : x < -20 ? -20 : x;
        y = y > 20 ? 20 : y < -20 ? -20 : y;
      } else {
        x = 0;
        y = 0;
      }
      this.offsetX = 0 - x;
      this.offsetY = 0 - y;
    },
  },
  mounted() {
    this.windowWidth = document.documentElement.clientWidth;
    this.windowHeight = document.documentElement.clientHeight;
    window.addEventListener("mousemove", this.updateMousePosition);
  },
  beforeDestroy() {
    window.removeEventListener("mousemove", this.updateMousePosition);
  },
};
</script>
  
  <style scoped lang="scss">
@import "../style/home.scss";
</style>
  